.hymn-scheduler {
  font-family: 'Nanum Square', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;

  .hymn-scheduler-week {
    .week--scroller {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      height: 10vh;
    }

    .week--scroller > div {
      width: 50px;
      text-align: center;
    }
  }

  .hymn-scheduler-month {
    width: 100%;
    display: flex;
    flex-direction: column;

  }

  .hymn-schedule-register-form {
    visibility: visible;
    opacity: 1;
    transition: visibility 300ms ease-in-out, opacity 300ms ease-in-out;
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #00000075;

    &.hidden {
      visibility: hidden;
      opacity: 0;
      transition: visibility 300ms ease-in-out, opacity 300ms ease-in-out;
    }

    & > .register-form--background {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 0;
    }

    & > form {
      background: whitesmoke;
      height: 50vh;
      width: 66vw;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      z-index: 1;
      border-radius: 10px;

      & > .hymn-form-row {
        display: flex;
        flex-direction: column;
        width: 80%;

        & > input {
          border: none;
          border-radius: 5px;
          background-color: transparent;
          font-size: 1.5em;
          padding: 1vh;
          width: 100%;
          outline: none;
          cursor: pointer;
        }

        & > input:focus {
          background-color: rgba(0, 0, 0, 0.41);
        }

        & ::placeholder {
          //font-family: 'Blank Sans', sans-serif;
        }
      }
    }
  }

  .add-schedule {
    position: absolute;
    bottom: 10%;
    right: 5%;
    border-radius: 100%;
    height: 50px;
    width: 50px;
    border: none;
    background: #00A3EE;
    color: white;
    font-size: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 3px 3px 3px #00000020;
  }

  .hymn-month-to-dos {
    display: flex;
    box-shadow: 3px 3px 3px #00000020;
    margin: 10px 5vw 0;
    border-radius: 20px;
    height: 3em;
    align-items: center;
    padding-left: 20px;
    border: 1px solid #00000020;

    .to-do-date {
      margin-left: auto;
    }

    .to-do-content {
      flex: 1;
      padding-left: 20px;
    }
  }
}

.scheduler-month-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 3em;
  background-color: #00A3EE;
  color: #FFFFFF;
  padding: 2vh 10vw;

  .scheduler-nav--prev {
    margin-right: auto;
  }

  .scheduler-nav--next {
    margin-left: auto;
  }

  .scheduler-indicator {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .scheduler-month-year {
    font-size: 0.5em;
    font-weight: lighter;
  }

  .scheduler-month-month {
    font-size: 2em;
    font-weight: bold;
  }

  &.short {
    background-color: white;
    color: #000;

    .scheduler-month-year {
      display: none;
    }
  }
}

.months-transition {
  display: flex;
  align-items: center;
  justify-content: center;
}

.scheduler-month-wrapper {
  display: grid;
  grid-template: repeat(7, 7vh)/repeat(7, 7vh);
  align-self: center;
  font-size: 0.9em;

  & > div {
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 2px;
    font-weight: bolder;
    color: #666666;
  }

  & > .day-indexes {
    border: none;
    font-size: 0.8em;

    &:first-child {
      color: red;
    }

    &:last-child {
      color: #4131ff;
    }
  }

  & > .prev-mth-days, .next-mth-days {
    opacity: 0.3;
  }

  & > .current-mth-days {
    color: #666666;

    &.selected {
      background: #B3B3B3;
      color: whitesmoke;
      border-radius: 5px;
      transition: color 300ms linear, background 150ms linear;
    }

    &.today {
      border: 2px solid #CCCCCC;
      border-radius: 5px;
    }
  }
}


